* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Inter, sans serif;
}

body {
  max-height: 100vh;
}
.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: rgb(0, 212, 212);
  height: 100px;
  padding: 0 40px;
  box-shadow: 0 0 10px 0 black;
  width: 100%;
  max-width: 700px;
  margin: 0 auto;
}
.nav {
  display: flex;
  justify-content: center;
  align-items: center;
}

.nav h1 {
  color: white;
  font-size: 2rem;
}

.earth {
  color: white;
  font-size: 2rem;
  margin-right: 10px;
}

.edit {
  color: white;
  font-weight: 500;
}

.form {
  width: 80%;
  margin: 20px auto 0 auto;
}

.text {
  width: 80%;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
}

.text-1 {
  width: 48%;
  height: 50px;
  font-size: 1.5rem;
  border-radius: 5px;
  border: 1px gray solid;
  font-family: Inter, sans serif;
  padding: 10px;
}

.button {
  display: flex;
  justify-content: center;
  margin-top: 20px;
  height: 50px;
}

.button button {
  width: 80%;
  border-radius: 5px;
  border-style: none;
  background-color: rgb(0, 212, 212);
  font-size: 1.5rem;
  font-weight: 700;
  color: white;
}

.button button:hover {
  cursor: pointer;
  background-color: rgb(1, 179, 179);
}

.box {
  border: 1px solid white;
  height: 70px;
  width: 70px;
  display: inline-block;
  margin: 10px;
  border-radius: 5px;
}

.meme {
  position: relative;
  max-width: 800px;
  margin: 20px auto;
  max-height: 700px;
}

.meme--image {
  width: 100%;
  border-radius: 3px;
  max-height: 100%;
}

.meme--text {
  position: absolute;
  width: 80%;
  text-align: center;
  left: 50%;
  transform: translateX(-50%);
  margin: 15px 0;
  padding: 0 5px;
  font-family: impact, sans-serif;
  font-size: 2em;
  text-transform: uppercase;
  color: white;
  letter-spacing: 1px;
  text-shadow: 2px 2px 0 #000, -2px -2px 0 #000, 2px -2px 0 #000,
    -2px 2px 0 #000, 0 2px 0 #000, 2px 0 0 #000, 0 -2px 0 #000, -2px 0 0 #000,
    2px 2px 5px #000;
}

.bottom {
  bottom: 0;
}

.top {
  top: 0;
}

